@import "../core/variables.scss";

.card {
  color: $bg-header;
  text-align: center;
  padding: 30px 0;
  text-decoration: none;
  :hover {
    filter: grayscale(80%);
    color: $bg-color;
  }

  &__title {
    font-size: 20px;
  }
  &__img {
    border: 4px solid $bg-header;
    border-radius: 70px;
    height: 140px;
  }
}
